class Popdown extends Component {
    constructor(props){
        super(props)
        this.onClick = this.documentClick.bind(this);
    }
    documentClick (e) {
        if (!this.popDown.contains(e.target)) {
            ReactDOM.unmountComponentAtNode(this.popDown.parentNode);
        }
    }
    componentDidMount() {
        document.addEventListener('click', this.onClick)
    }

    componentWillUnmount() {
        document.removeEventListener('click', this.onClick);
    }
    render(){
        switch (this.props.i){
        case 'JavaScript':  
            return (
                <div className = 'popdown' style={{backgroundColor: 'white'}} ref={pd=>this.popDown = pd}>
                    <p><code className='content'>Yay, JavaScript, my favourite language. <br/>&nbsp;One language to code them all, doesn't this sounds great? 
                    <br />&nbsp;Started frontend only, with typical length of code being one single line, 
                    <br />&nbsp;modern JavaScript had increadibly become the most popular language.
                    <br />&nbsp;For those frameworks and libraries I have touched:<br />
                    &nbsp;Frontend: React (this page is using), Angular, Vue, jQuery, D3.js<br />
                    &nbsp;Backend: Express, and of course the great Node.js itself<br />
                    &nbsp;Desktop: Electron<br />
                    &nbsp;Mobile: React Native</code></p>
                </div>
            );
        case 'Python': 
            return (
                <div className = 'popdown' style={{backgroundColor: 'white'}} ref={pd=>this.popDown = pd}>
                    <p><code className='content'>Python, the "most popular language" three years ago.
                    <br/>&nbsp;Of course Python itself haven't degenerated or what
                    <br />&nbsp;Just the growth of JavaScripts these three years
                    (Especially, after <br/>&nbsp;the release of ES6) had been so unbelievable to beat every other.
                    <br />&nbsp;I used Python a lot for my final year project in university,<br />
                    &nbsp;It was about using machine learning algorithms<br/>&nbsp;to predict student's performance<br />
                    &nbsp;Which means I have touched on these libraries:<br />
                    &nbsp;numpy, pandas, matplotlib, sklearn,<br />
                    &nbsp;and the globally beloved tensorflow</code></p>
                </div>
            );
        case 'C#':
            return (
                <div className = 'popdown' style={{backgroundColor: 'white'}} ref={pd=>this.popDown = pd}>
                    <p><code className='content'>To be honest, I haven't learnt any C#
                    <br/>&nbsp;How dare I put it here?
                    <br/>&nbsp;You will find the language features and grammars of C# and JavaScript
                    <br />&nbsp;Or to say, on JavaScript compiller, TypeScript,
                    <br />&nbsp;Are just so similar to each other<br />
                    &nbsp;I mainly used C# for server side development (ASP.NET MVC)</code></p>
                </div>
            );
        case 'PHP':
            return (
                <div className = 'popdown' style={{backgroundColor: 'white'}} ref={pd=>this.popDown = pd}>
                    <p><code className='content'>And PHP. Yes. The most easy-to-learn server side language
                    <br/>&nbsp;But I don't want to overestimate my skills on PHP,<br/> since every "simple" language will have<br/>
                    &nbsp;their own art of coding.</code></p>
                </div>
            );
        case 'C++':
            return (
                <div className = 'popdown' style={{backgroundColor: 'white'}} ref={pd=>this.popDown = pd}>
                    <p><code className='content'>Failed score for C++
                    <br/>&nbsp;I don't even dare to say "I know C++" - but well, who dares?<br/>
                     &nbsp;It might be the most powerful yet difficult language<br/> that a student can touch on</code></p>
                </div>
            );
        case 'Java':
            return (
                <div className = 'popdown' style={{backgroundColor: 'white'}} ref={pd=>this.popDown = pd}>
                    <p><code className='content'>One incredible thing: I don't know Java, I haven't learnt Java, 
                    <br/>&nbsp;and I don't even plan to learn it - at least for now.<br/>
                    </code></p>
                </div>
            );
        default:
        return (<p></p>);
        }
    }
}

class Para extends Component {
    handleClick(e) {
        if (e.target.childNodes[1])
            ReactDOM.render(<Popdown i={this.props.i}/>, e.target.childNodes[1]);
    }
    render () {
        return (
            <div onClick={e=> this.handleClick(e)}>
                {this.props.i}
                <div style={{zIndex:5}}></div>
            </div>  
        );
    }
}


