import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import logo from './embed.svg';
import './App.css';
import $ from 'jquery';
import Html from './htmlcontent.jsx' ;
class Header extends Component {
    constructor() {
        super();
        this.state = {
            sT: 0,
            size: 13,
        };
    }
    
    componentDidMount() {
        document.addEventListener('scroll', () => {
            this.setState((prevState) =>{
                if (prevState.sT>this.props.winH-120 && this.state.sT >this.props.winH-120)
                    return {sT:this.props.winH, size: 7};
                else 
                    return $(document).scrollTop() >= this.props.winH-120 ? {sT: this.props.winH-120, size: 7} :{sT: $(document).scrollTop(), size: 13-7*($(document).scrollTop()/(this.props.winH-120))}
            });
        });
    }

    componentWillUnmount() {
        document.removeEventListener('scroll');
    }

    render() {
        return (
            <header className="App-header" style={{ height: this.props.winH - this.state.sT }}>
                <div id="header">
                    <h2 className="im"  style={{ fontSize: this.state.sT > 350 ? 0 : '4vw'}}>This is</h2>
                    <h1 className="App-title"  style={{ fontSize: this.state.size + 'vw', paddingLeft: this.state.size *20, paddingRight: this.state.size *20}}>Wu Yin Lucius.</h1>
                    <img src={logo} className="App-logo" alt="logo" />
                </div>
                <div className="code" style={{visibility: this.state.sT>this.props.winH*0.3? 'hidden': 'visible'}}>
                <code>
                /**<span style={{color: '#3f9cd6'}}>@description</span>&nbsp;He who call himself  <br/>&nbsp;* a half-way - not even a beginner -<br/>&nbsp;* full stack developer */
                </code></div>
            </header>
        );
    }
}

class Main extends Component {
    
    render() {
        
        return (
            <div className="App-main" >
                <span id='home'></span>
                <Content />
                <Nav />

            </div>
        );
    }
}

class Footer extends Component {
    render() {
        return (
            <footer className="App-footer">
                <p></p>
            </footer>
        );
    }
}

class Content extends Component {
    render() {
        const mainStyle = {
            paddingTop: window.innerHeight,
        };
        return (
            <div className="App-content" style={mainStyle}>
                <p  style={{paddingTop: 40}}></p>
                <Html />
            </div>
        );
    }
}

class Nav extends Component {
    constructor(props){
        super(props);
        this.state = {
            anchorPos: [],
            anchorStat: [],
        };
        this.pageScroll = this.pageScroll.bind(this);
        
    }

    pageScroll() {
        let currentTop = $(document).scrollTop();
        let anchorStatus = this.state.anchorPos.map((a)=>{return ((currentTop-a) > -190 && (currentTop-a) < 360);});
        this.setState({anchorPos: this.state.anchorPos, anchorStat: anchorStatus});
    }

    getAnchorPos(){
        const anchorPos = [1070, 1500, $('#_resume').position().top,$('#_portfolio').position().top, 300];
        return anchorPos;
    }

    componentDidMount() {
        $(document).scroll(this.pageScroll);
        this.setState({anchorPos: this.getAnchorPos(), anchorStat: []})
    }
    componentWillUnmount() {
        document.removeEventListener('onScroll', this.pageScroll);
    }
    
    renderLi(i){
        const list = ['Profile', 'Skills', 'Education', 'Portfolio', 'Home'];
        const linklist = ['#_profile', '#_skills', '#_resume', '#_portfolio', '#home'];

        return (
        <li ><a href={linklist[i]} className={this.state.anchorStat[i] ? 'active' : ''}>{list[i]}</a></li>
        );
    }
    render() {
        return (
            <div className="App-nav">
                <nav>
                    <ul>
                        {this.renderLi(0)}
                        {this.renderLi(1)}
                        {this.renderLi(2)}
                        {this.renderLi(3)}
                        {this.renderLi(4)}
                    </ul>
                </nav>
            </div>
        );
    }
}

class App extends Component {
    render() {
        const winHeight = window.innerHeight;
        return (
            <div className="App">
                <Header winH={winHeight} />
                <Main />
                <Footer />
            </div>
        );
    }
}

export default App;



